<template>
  <div>
    <div class="topButton">
      <el-button size="mini" type="primary" @click="shenpigo()"></el-button>
      <el-button size="mini" type="danger" @click="bohui()"></el-button>
    </div>
    <div class="list">
      <el-table
        :data="needPlanS.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%"
        highlight-current-row
        border
        @current-change="handleCurrentsChange"
      >
        <el-table-column label="" prop="needPlanNum"></el-table-column>

        <el-table-column label="" prop="materialName"></el-table-column>

        <el-table-column label="" prop="num"></el-table-column>

        <el-table-column label="" prop="materialPassTime"></el-table-column>

        <el-table-column label="" prop="price"></el-table-column>
        <el-table-column label="" prop="num_price"></el-table-column>



        <el-table-column label="" prop="needPlanState"></el-table-column>

        <el-table-column label="" prop="materialTypeName"></el-table-column>
        <el-table-column label="" prop="rejectRemark"></el-table-column>
        <el-table-column label="" prop="remark"></el-table-column>
      </el-table>

      <!--  -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="needPlanS.length"
        ></el-pagination>
      </div>
    </div>

    <div class="delete" v-if="flag3">
      <div class="title">
        <span></span>
        <i class="el-icon-close" @click="clossB()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span></span>
        <el-input
          type="textarea"
          autosize
          placeholder=""
          v-model="bohuidata.rejectRemark"
          style="width:330px;"
        ></el-input>
      </div>
      <div class="deleteButton">
        <el-button size="mini" @click="clossB()"></el-button>
        <el-button size="mini" type="danger" @click="rejected()"></el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag3"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      needPlanS: [],
      currentPage: 1, //
      pagesize: 10,
      domaterial: [],
      flag3: false,
      bohuidata: [
        {
          id: "",
          needPlanState: "",
          rejectRemark: ""
        }
      ],
      shenpi: [
        {
          id: "",
          needPlanState: ""
        }
      ]
    };
  },
  created() {
    let _this = this;
    this.$axios
      .get("/passNeedplans")
      .then(function(res) {
        if (res.data) {
          _this.needPlanS = res.data;
          // console.log(res.data)
        }
      })
      .catch(function(err) {
        if (err.response) {
          console.log(err.response);
        }
      });
  },
   inject: ["reload"],
  methods: {
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentsChange(val) {
      this.currentRow = val;

      //   console.log(this.changeDept)
      // this.changeList = []
      this.bohuidata = val;
      this.shenpi = val;
      // console.log(this.bohuidata.id)
      // console.log(this.shenpi.id)

    },

    handleSizeChange: function(size) {
      this.pagesize = size;
      // console.log(this.pagesize)  //
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage)  //
    },
    bohui() {
      
      if(this.bohuidata.id){
          this.flag3 = !this.flag3;
      }else {
          this.$message('')
      }
    },
    clossB() {
      this.flag3 = !this.flag3;
      this.$message("");
    },
    rejected() {
      let _this = this;
      this.$axios
        .put("/needplanState", {
          id: _this.bohuidata.id,
          needPlanState: _this.bohuidata.needPlanState,
          rejectRemark: _this.bohuidata.rejectRemark
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag3 = !this.flag3;
            this.reload();
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
            // this.$message("");
          }
        });
    },

    shenpigo() {
      // this.shenpi.materialState = "";
      let _this = this;
      this.$axios
        .put("/needplanState", {
          id: _this.shenpi.id,
          needPlanState: _this.shenpi.needPlanState
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.reload();
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
            // this.$message("");
          }
        });
    }
  }
};
</script>

<style>
.el-table-column {
  text-align: center;
}
.topButton {
  width: 100%;
  height: 50px;
  line-height: 50px;
}
.list {
  position: relative;
}
.page {
  /* margin-top: 20px;
    margin-left: 800px; */
  position: absolute;
  right: 0;
  bottom: -50px;
}
.delete {
  position: absolute;
  z-index: 1001;
  width: 430px;
  height: 240px;
  border: 1px solid #dcdfe6;
  left: 0;
  top: -50px;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
}
.title {
  width: 100%;
  height: 45px;
  line-height: 45px;
}
.title span {
  float: left;
  margin-left: 20px;
}
.delete .title i {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
}
.delete .main {
  height: 50px;
  width: 100%;
  line-height: 50px;
  margin-left: 50px;
}
.delete .main i {
  color: #e6a23c;
  font-size: 20px;
  margin-right: 10px;
}
.delete .main span {
  margin-bottom: 5px;
}

.delete .deleteButton {
  float: right;
  margin-right: 20px;
  margin-top: 100px;
}
.popContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 500;
}
</style>


