<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" :before-close="hideDialog">
        <div class="add">
            <el-form ref="form" :model="formData" label-width="80px" :rules="rules">
                <el-form-item label="">
                    <el-input v-model="formData.userName"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formData.loginName" v-if="formData.id" disabled></el-input>
                    <el-input v-model="formData.loginName" v-else></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formData.password" type="password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formData.rePassword" type="password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formData.userMobile" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formData.userSex" placeholder="">
                        <el-option :v-for="sexEnum"></el-option>
<!--                        <el-option label="" value="1"></el-option>-->
<!--                        <el-option label="" value="2"></el-option>-->
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formData.deptNo" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
        </div>
            <div slot="footer" align="center">
                <el-button size="mini" @click="$emit('hideDialog')"></el-button>
                <el-button type="primary" size="mini" @click="submit('form')"></el-button>
            </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "addUser",
        props: {
            rowData: {
                type: Object,
                default: function () {
                    return {}
                }
            }
        },
        data() {
            return {
                dialogVisible: true,
                formData: {
                    id:'',
                    userName: '',
                    loginName: '',
                    password:'',
                    rePassword:'',
                    userMobile:'',
                    userSex:'',
                    deptNo:''
                },
                title:'',
                sexEnum:{
                    1:'',
                    2:''
                },
                rules: {
                    userName: [{required: true, message: "", trigger: 'blur'}],
                    loginName: [{required: true, message: "", trigger: 'blur'},
                        { max: 10, message: "10", trigger: "blur" }]
                }
            }
        },
        created() {
            this.formData=Object.assign({},this.formData,this.rowData)
            if(this.formData.id){
                this.title=''
            }
        },
        methods: {
            hideDialog() {
                this.$emit('hideDialog')
                this.$emit('query')
            },
            submit(addForm) {
                this.$refs[addForm].validate(valid => {
                    if (valid) {
                        if(this.formData.id){
                            this.$axios.post("/user/update", {
                                id: this.formData.id,
                                loginName:this.formData.loginName,
                                userName: this.formData.userName,
                                password: this.formData.password,
                                userMobile:this.formData.userMobile,
                                userSex: this.formData.userSex,
                                deptNo: this.formData.deptNo
                            }).then(res => {
                                if (res.data.code === 200) {
                                    this.$message.success("");
                                    this.hideDialog()
                                } else {
                                    this.$message.error(res.data.message);
                                }
                            }).catch(err => {
                                this.$message.error("")
                            });
                        }else {
                            this.$axios.post("/user/add", {
                                loginName:this.formData.loginName,
                                userName: this.formData.userName,
                                password: this.formData.password,
                                userMobile:this.formData.userMobile,
                                userSex: this.formData.userSex,
                                deptNo: this.formData.deptNo
                            }).then(res => {
                                if (res.data.code === 200) {
                                    this.$message.success("");
                                    this.hideDialog()
                                } else {
                                    this.$message.error(res.data.message);
                                }
                            }).catch(err => {
                                this.$message.error("")
                            });
                        }
                    }else {
                        return false;
                    }
                });

            }
        }
    }
</script>

<style scoped>
    .add {
        margin-right: 40px;
        /*margin-top: 30px;*/
        margin-left: 10px;
    }
</style>


