<template>
  <div>
    <div class="topButton">
      <el-button size="mini" type="primary" @click="handleDownload" :loading="downloadLoading"></el-button>
    </div>
    <!--  -->
    <div class="list">
      <el-table
        :data="material.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%"
        highlight-current-row
        border
        @current-change="handleCurrentsChange"
      >
        <el-table-column label="" prop="materialNum"></el-table-column>

        <el-table-column label="" prop="materialName"></el-table-column>

        <el-table-column label="" prop="materialPostTime"></el-table-column>

        <el-table-column label="" prop="materialPassTime"></el-table-column>

        <el-table-column label="" prop="price"></el-table-column>

        <el-table-column label="" prop="materialState"></el-table-column>

        <el-table-column label="" prop="materialTypeName"></el-table-column>
        <el-table-column label="" prop="rejectRemark"></el-table-column>
        <el-table-column label="" prop="remark"></el-table-column>
      </el-table>

      <!--  -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="material.length"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      material: [],
      currentPage: 1, //
      pagesize: 10,
      downloadLoading:false,
    };
  },
  created() {
    let _this = this;
    this.$axios
      .get("/passMaterials")
      .then(function(res) {
        if (res.data) {
          _this.material = res.data;
        }
      })
      .catch(function(err) {
        if (err.response) {
          console.log(err.response);
        }
      });
  },
  inject: ["reload"],
  methods: {
    
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
      setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentsChange(val) {
      this.currentRow = val;

      this.changematerial = val;
    },
    handleDownload() {
      this.downloadLoading = true;
      console.log(this.material);
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/vendor/Export2Excel");
        const tHeader = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];
        const filterVal = [
          "materialNum",
          "materialName",
          "materialPostTime",
          "materialPassTime",
          "price",
          "materialState",
          "materialTypeName",
          "rejectRemark",
          "remark"
        ];
        const list = this.material;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "excel");
        this.downloadLoading = false;
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};
</script>

<style>
.topButton {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding-bottom: 20px;
}
.list {
  position: relative;
}
.page {
  /* margin-top: 20px;
    margin-left: 800px; */
  position: absolute;
  right: 0;
  bottom: -50px;
}
</style>


